import React, { useEffect, useState } from 'react';
import { connect } from 'dva';

import { DecryptDes, GetCookie } from '../../../utils/func';
import { fetchUpdatePlanProInfo, fetchTrainProLogin } from '../../../services/detail';
import { UserState } from '../model';
import { AssetAllocationType } from '../../datas';
import Box from './Box';
import Switch from '../../../components/Switch';
import DatePicker from '../../../components/DatePicker';
import ActiveInfo from '../../../components/ActiveInfo';
import styles from './index.less';

interface TrainPulanOperationProps {
  user: UserState;
}


const TrainPulanOperation: React.SFC<TrainPulanOperationProps> = (props) => {
  const { user } = props;
  const { userData } = user;
  const [effectiveDate, setEffectiveDate] = useState<any>();
  const [assetAllocation, setAssetAllocation] = useState<number>(0);

  // 
  const handleAllocation = (val: number) => {
    setAssetAllocation(val)
    fetchUpdatePlanProInfo({
      userId: DecryptDes(GetCookie('externaluserid')),
      unionId: DecryptDes(GetCookie('unionid')),
      updKind: 'asset',
      assetAllocation: val
    })
  }

  // 
  const handleChangeDate = (val) => {
    fetchUpdatePlanProInfo({
      userId: DecryptDes(GetCookie('externaluserid')),
      unionId: DecryptDes(GetCookie('unionid')),
      updKind: 'date',
      expireDate: val
    })
  }

  useEffect(() => {
    if (userData) {
      setAssetAllocation(userData.family.assetAllocation)
      fetchTrainProLogin({
        userId: localStorage.getItem('userId'),
        unionid: DecryptDes(GetCookie('unionid')),
        remarkmobile: ''
      })
    }
  }, [user])

  return (
    <Box title="" extra={<div style={{color: '#1890FF'}}>PC &gt;</div>}>
      <div className={styles.trainPulanOperation}>
        <ActiveInfo title="" value={userData && `${userData.pro.city}`} />
        <ActiveInfo title="" value={userData && `${userData.family.age}`} />
        <ActiveInfo title="" value={userData && `${userData.pro.income}`} />
        <ActiveInfo title="" value={userData && `${userData.pro.AnnualSurplusBegin}`} />
        <ActiveInfo title="" value={userData && `${userData.pro.currentAssetsBegin}`} />
        <ActiveInfo title="" value={userData && `${userData.train.planStage.CreationTime}${userData.train.planStage.DP1}`} />
        <ActiveInfo title="" value={userData && `${userData.family.effectiveRest}`}/>
        <ActiveInfo title="" value={
          <div style={{width: '5rem'}}>
            <DatePicker value={userData && userData.family.expireDate} showSuffixIcon onChange={handleChangeDate} />
          </div>
        }/>
        <ActiveInfo title="" value={userData && `${userData.family.lastLoginTime}`} />
        <ActiveInfo title="" value={userData && `${userData.family.recommendScheme.scheme}`} />
        <ActiveInfo title="FYP" value={userData && `${userData.train.recommendProtectSolutionFYP}`} />
        <ActiveInfo title="" value={
          <div style={{display: 'flex', alignItems: "center"}}>
            {userData && `${AssetAllocationType[assetAllocation]}`}
            <Switch inactiveValue={0} activeValue={1} onChange={handleAllocation} value={assetAllocation}/>
          </div>
        }/>
      </div>
    </Box>
  )
}

export default connect(({ user }: { user: UserState }) => ({
  user,
}))(TrainPulanOperation);